@import '../../../assets/styles/mixins/breakpoints.scss';
@import '../../../assets/styles/variables.scss';

.searchAndSortBar {
  background: rgba(97, 127, 225, 0.1);
  border-radius: 15px;
  width: 100%;
  padding: 8px 16px;
  min-width: 190px;
  box-shadow: 0px 4px 10px rgba(42, 51, 110, 0.3);

  @include medium {
    padding: 20px 24px;
  }

  @include large {
    min-width: 250px;
    padding: 48px 24px;
  }

  &__searchBar {
    margin-bottom: 20px;

    &__input {
      position: relative;

      &__field {
        width: 100%;
        height: 45px;
        background: rgba(97, 127, 225, 0.1);
        border: none;
        border-radius: 10px;
        padding: 0 10px 0 40px;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        color: $secondary;
      }

      &__field::placeholder {
        color: $secondary;
      }

      &__icon {
        width: 40px;
        height: 45px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;

        &__image {
          width: 45%;
          height: 45%;
        }
      }
    }

    &__clearTextButton {
      margin-top: 5px;
      font-size: 12px;
      color: $light-grey;
      pointer-events: none;
      transition: color 0.5s;

      &__active {
        pointer-events: auto;
        cursor: pointer;
        color: $primary;
      }

      &__active:hover {
        color: #0500ff;
      }
    }
  }

  &__sortTypes {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    &__label {
      display: flex;
      align-items: center;
      margin-right: 0;

      @include medium {
        width: 100%;

        &:not(:last-child) {
          margin-bottom: 24px;
        }
      }
    }

    &__active {
      font-weight: bold;
    }

    &__title {
      margin-left: 5px;
      background: none;

      @include large {
        font-size: 18px;
      }
    }
  }
}
